<template>
  <div class="group-list-container" :class="edit">
    <!-- 分组列表 -->
    <div class="group-list-choose-txt">{{lang.con_group}}</div>
    <div class="group-list" v-on:scroll="groupListScroll">
      <div is="group-card" v-for="item in groupListCopy" v-bind:key="item.groupId" v-bind:item="item" :selected="getSelected(item.groupId)" v-on:scroll="groupListScroll">
      </div>
    </div>
    <div class="both-center btn-add">
      <img id="add-group" class="img-add" src="./static/images/add.png" />
      <div class="both-center btn-item">
        <div class="btn-text">{{lang.cen_con+'&nbsp;&nbsp;'+'('+nums+')'}}
        </div>
      </div>
      <div class="both-center btn-item">
        <div class="btn-text btn-delete">{{lang.remo_gro+'&nbsp;&nbsp;'+'('+nums+')'}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import {
  MAIN_UI_LONGPRESS_STATE,
  GROUP_LIST_SCROLLTOP
} from './../../../store/mutations-type'

export default {
  data: function () {
    return {
      lang: '',
      nums: 2,
      isScroll: false,
      afterScrollTimer: undefined
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$el.querySelector('.group-list').scrollTop = vm.$store.state.groupListScrollTop
    })
  },
  created() {
    this.lang = this.$language
  },
  mounted() {
    let timer = undefined, self = this
    longPress(this.$el.querySelector('.group-list'), timer, function () {
      if (!self.isScroll) {
        console.log("長按觸發     " + self.isScroll)
        self.$store.commit(MAIN_UI_LONGPRESS_STATE, true)
      }
    })
  },
  computed: {
    ...mapState(['groupListCopy', 'groupListSelectStates']),
    edit: function () {
      return {
        'edit': this.$store.state.mainUILongPressState
      }
    },
    nums: function () {
      let num = 0
      for (let i = 0, j = this.groupListSelectStates.length; i < j; i++) {
        if (this.groupListSelectStates[i].selected) {
          num++
        }
      }
      return num
    }
  },
  methods: {
    groupListScroll: function () {
      this.isScroll = true
      let self = this
      clearTimeout(self.afterScrollTimer)
      this.afterScrollTimer = setTimeout(function () {
        self.isScroll = false
      }, 300);
    },
    getSelected: function (index) {
      let value = false
      for (let i = 0, j = this.groupListSelectStates.length; i < j; i++) {
        if (index === this.groupListSelectStates[i].groupId) {
          value = this.groupListSelectStates[i].selected
        }
      }
      return value
    }
  },
  beforeRouteLeave(to, from, next) {
    this.editing = false
    this.$store.commit(MAIN_UI_LONGPRESS_STATE, false)
    this.$store.commit(GROUP_LIST_SCROLLTOP, this.$el.querySelector('.group-list').scrollTop)
    next()
  }
}
</script>


<style>
.group-list-container {
  position: relative;
  height: 88%;
  width: 100%;
}

.group-list-choose-txt {
  position: relative;
  width: 100%;
  height: 6%;
  display: -webkit-flex;
  display: flex;
  align-items: center;
  color: #989aa1;
  padding-left: 4%;
  font-size: 1.6rem;
}

.group-list {
  margin-top: 1%;
  position: relative;
  height: 84%;
  width: 100%;
  flex-wrap: wrap;
  overflow-x: hidden;
  overflow-y: auto;
}

.both-center {
  display: -webkit-flex;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-add {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 9%;
  line-height: 9%;
  background-color: #fff;
}

.btn-item {
  display: none;
  width: 50%;
  height: 100%;
  text-align: center;
}

.btn-text {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.img-add {
  height: 50%;
}

.edit .img-add {
  display: none;
}

.edit .btn-item {
  display: block;
  width: 50%;
  font-size: 2rem;
  line-height: 100%;
  color: rgb(0, 121, 255);
}

.edit .btn-delete {
  color: #ff0101;
}
</style>



